import axios from "axios";



const url = '/contacts'



export const getAllContact = async ({token}) => {
    let{data} = await axios.post(`${url}/all`,{token})
    return data
}

export const addContact = async ({name, number, token}) => {
    let{data} = await axios.post(`${url}/add`,{name, number,token})
    return data
}

export const deleteRequest = async ({id, token}) => {
    await axios.delete(url, {data: {id,token}})
}


export const editRequest = async ({name,number,id}) => {
    let {data} = await axios.post(`${url}/edit`, {name,number, id})
    return data
}



