import { loginUser } from "../requests/loginUser"

const user = (state = false, action) => {
    switch(action.type){
        case 'LOGIN':
            if(!action.data){
              window.localStorage.removeItem('user')
            return false
        }
              window.localStorage.setItem('user', JSON.stringify(action.data))
            return true
        case 'LOGOUT':           
            window.localStorage.removeItem('user')
            return false
        default:
            return state
    }
}

export const dispatchLogin = ({username,password, token}) => {
    return async (dispatch) => {
        let data = await loginUser({username,password, token})
       if(!data){
           dispatch({
               type:'SETMESSAGE',
               message:'Wrong username or password'
           })
           setTimeout(() => {
            dispatch({
                type:'SETMESSAGE',
                message:''
            })
           }, 5000)
       }
        dispatch({
            type: 'LOGIN',
            data
        })
    }
}

export const dispatchLogout = () => {
    return  (dispatch) => {
        dispatch({
            type: 'LOGOUT'
        })
    }
} 

export default user