const messageReducer = (state='', action) => {
    switch(action.type){
        case 'SETMESSAGE':
            return action.message
        case 'SETUSERMESSAGE':
            return action.message
        default:
            return state
    }
}

export const setMessage = (message) => {
    return (dispatch) => {
        dispatch({
            type: 'SETMESSAGE',
            message
        })
    }
}


export const setUserMessage = (message) => {
    return (dispatch) => {
        dispatch({
            type: 'SETUSERMESSAGE',
            message
        })
    }
}





export default messageReducer