import './loginForm.css'
import { useState } from "react"
import { connect } from "react-redux"
import { addContactReducer } from "../reducers/contactsReducer"
import { setMessage } from '../reducers/messageReducer'



const AddContactForm = ({add, setMessage}) => {
    const [name, setName] = useState('')
    const [number, setNumber] = useState('')

    let userFromStorege = JSON.parse(window.localStorage.getItem('user'))

    const addContact = (e) => {
        e.preventDefault()
            let resultName = name.match(/[\d]/g)
            let resultNums = number.match(/[\D]/g)

            if(resultNums || resultName){
                setMessage('Name must be text and Number only Numbers')
                setTimeout(() => {
                    setMessage('')
                }, 5000)
                return
            }

            add({name, number, token: userFromStorege.token})

        setName('')
        setNumber('')
    }

    return (
        <div className='addMain'>
            <h2>Add Contact</h2>
            <form onSubmit={(e) => addContact(e)}>
                <div className='div'>
                    <input
                        value={name}
                        onChange={({target}) => setName(target.value)}
                        required
                        type='text'
                        autoComplete="off"
                        className='input'
                    />
                    <label className='label'>
                        <span className='span'>Name</span>
                    </label>
                </div>
                <div className='div'>
                    <input
                        value={number}
                        onChange={({target}) => setNumber(target.value)}
                        required
                        autoComplete="off"
                        className='input'
                    />
                    <label className='label'>
                        <span className='span'>Number</span>
                    </label>
                </div>
                <button className='addButton' type="submit">Add</button>
            </form>
        </div>
    )
}


const mapDispatchToProps = {
    add: addContactReducer, setMessage
}



export default connect(null, mapDispatchToProps)(AddContactForm)