import './phonebook.css'
import { useEffect, useState } from "react"
import { connect } from "react-redux"
import { deleteContactReducer, editContactReducer, initContacts } from "../reducers/contactsReducer"
import AddContactForm from "./addContactForm"
import { setMessage } from '../reducers/messageReducer'

const Contacts = ({init, contacts, deleteContact, editContact, setMessage}) => {
    const [search, setSearch] = useState('')
    const [edit, setEdit] = useState('')
    const [name, setName] = useState('')
    const [number, setNumber] = useState('')
    useEffect(() => {
        setMessage('')
        let user = JSON.parse(window.localStorage.getItem('user'))
        init({token: user.token})
    }, [init, setMessage])
    

    let user = JSON.parse(window.localStorage.getItem('user'))

    let contactsToShow = search.length > 0 && !search.match(/[\d]/g)
    ? contacts.filter(f => f.name.indexOf(search) >= 0)
    : !search.match(/[\D]/g)
    ? contacts.filter(f => String(f.number).indexOf(search) >=0)
    : contacts

    const editFunc = ({id}) => {
        let resultName = name.match(/[\d]/g)
        let resultNums = number.match(/[\D]/g)

        if(resultNums || resultName){
            setMessage('Name must be text and Number only Numbers')
            setTimeout(() => {
                setMessage('')
            }, 5000)
            return
        }

        editContact({name, number, id})
        setName('')
        setNumber('')
        setEdit('')
    }
    

    return(
        <div className="phonebook">
                <AddContactForm />
            
            <div className='contacts'>
                <h2 style={{marginBottom: '10px'}}>Contacts</h2>
            <div className='div'>
                    <input 
                        value={search}
                        type='text'
                        onChange={({target}) => setSearch(target.value)}
                        required
                        className='input'
                        autoComplete='off'
                    />
                    <label className="label" htmlFor="username">
                        <span className="span">search</span>
                    </label>
                </div>
                {contactsToShow.map(mp => {
                    if(edit === mp.id){
                        return(
                            <div key={mp.id} >
                                
                                <div className='div'>
                                    <input 
                                        value={name}
                                        onChange={({target}) => setName(target.value)}
                                        required
                                        type='text'
                                        autoComplete="off"
                                        className='input'
                                     />
                                    <label className='label'>
                                        <span className='span'>New username</span>
                                    </label>
                                </div>
                                <div className='div'>
                                <input
                                    value={number}
                                    onChange={({target}) => setNumber(target.value)}
                                    required
                                    autoComplete="off"
                                    className='input'
                                />
                                    <label className='label'>
                                        <span className='span'>New number</span>
                                    </label>
                                </div>
                                    <div style={{display: 'flex', justifyContent:'space-between', marginBottom:'10px', marginTop: '15px'}}>
                                        <button 
                                        onClick={() => editFunc({id: mp.id})}
                                        className='editButton'>edit</button>
                                        <button className='cancelButton' onClick={() => setEdit('')}>Cancel</button>
                                    </div>
                            </div>
                        )
                    }else{
                        return(
                            <div key={mp.id}>
    
                                <p> {mp.name} - {mp.number}</p>
                                <div style={{display: 'flex', justifyContent:'space-between'}}>
                                 <button className='editButton' onClick={() => setEdit(mp.id)}>edit</button>
                                 <button className='deleteButton' style={{cursor: 'pointer'}} onClick={() =>deleteContact({id: mp.id, token: user.token})}>X</button>
                                </div>
                            </div>
                        )
                    }
                })}
            </div>
        </div>
    )
}


const mapStateToProps = (state) => {
    return {
        contacts: state.contactsReducer,
        user: state.user
    }
}

const mapDispatchToProps = {
    init: initContacts,
    deleteContact: deleteContactReducer,
    editContact: editContactReducer,
    setMessage
}

export default connect(mapStateToProps, mapDispatchToProps)(Contacts)