import './registerForm.css'
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { registerUser } from '../requests/register'

const RegisterForm = () => {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [confPassword, setConfPassword] = useState('')
    const navigate = useNavigate()

const register = async (e) => {

    e.preventDefault()
        if(password === confPassword){
            let data = await registerUser({username, password})
            setUsername('')
            setPassword('')
            setConfPassword('')
            console.log(data)
            if(data){
                return navigate('/login')
            }
        }
        setConfPassword('')
} 

    return(
        <div className='register'>
            <h1>Register</h1>
            <form className="registerForm" onSubmit={(e) => register(e)} autoComplete='off'>
                <div className='div'>
                    <input 
                        className='input'
                        value={username}
                        type='text'
                        name='username'
                        onChange={({target}) => setUsername(target.value)}
                        required
                    />
                    <label className='label'  htmlFor="username">
                        <span className='span'>Username</span>
                    </label>
                </div>
                <div className='div'>
                    <input 
                        className='input'
                        type='password'
                        name='password'
                        value={password}
                        onChange={({target}) => setPassword(target.value)}
                        required
                    />
                    <label className='label' htmlFor="password">
                        <span className='span'>Password</span>
                    </label>
                </div>
                <div className='div'>
                    <input className='input'
                        type='password'
                        name='passwordConfirm'
                        value={confPassword}
                        onChange={({target}) => setConfPassword(target.value)}
                        required
                    />
                    <label className='label' htmlFor="passwordConfirm">
                        <span className='span'>Confirm Password</span>
                    </label>
                </div>
                <div>
                    <button className='registerButton' type="submit">register</button>
                </div>
            </form>
            <button onClick={() => navigate('/login')} className='loginButton'>login</button>
        </div>
    )
}


export default RegisterForm