import './App.css'
import { useEffect } from "react";
import {Routes, Route} from 'react-router-dom'
import LoginForm from "./components/loginForm";
import RegisterForm from "./components/registerForm";
import {connect} from 'react-redux'
import { dispatchLogin, dispatchLogout } from "./reducers/userLogined";
import Contacts from "./components/contacts";



const App = ({user, dispatchLogin, dispatchLogout, message}) => {
useEffect(() => {
  let userFromStorage = JSON.parse(window.localStorage.getItem('user'))
  if(userFromStorage){
    dispatchLogin({token:userFromStorage.token})
  }
}, [dispatchLogin,user])


  return(
    <div>
      {message? <div className='message'> 
        <h3 className='messageText'>{message}</h3>
      </div>:
      null}
      <header>
        <h1 style={{color: 'rgb(133, 29, 127)'}}>Phonebook</h1>
      </header>
        {user
        ? <div>
          <button 
          onClick={dispatchLogout} 
          style={{float:'right', marginRight:'100px'}}>Log out</button>
            <div style={{display: 'flex', justifyContent:'center'}}>
              <Contacts />
            </div>
          </div>
        : <div className="user">
            <Routes>
               <Route path='/login' element={<LoginForm forMessage={true} />} />
               <Route path='/' element={<RegisterForm />} />
          </Routes>
          </div>}
    </div>
  )
} 

const mapStateToProps = (state) => {
  return {
    user: state.user,
    message: state.messageReducer
  }
}

const mapDispatchToPtops = {
  dispatchLogin, dispatchLogout
}

export default connect(mapStateToProps, mapDispatchToPtops)(App);
