import { addContact, deleteRequest, editRequest, getAllContact } from "../requests/contactRequests"

const contactsReducer = (state = [], action) => {
    switch(action.type){
        case 'INIT':
            return action.data
        case 'ADD':
            return [...state, action.data]
        case 'EDIT':
            if(!action.data){
                window.localStorage.removeItem('user')
                return state
            }
            let updatedContact = action.data
            return state.map(mp => mp.id === action.data.id ? updatedContact : mp)        
        case 'DELETE':
            return state.filter(f => f.id !== action.id)
        default:
            return state
    }
}


export const initContacts = ({token}) => {
    return async (dispatch) => {
        let data = await getAllContact({token})
        dispatch({
            type: 'INIT',
            data
        })
    }
}

export const addContactReducer = ({name,number,token}) => {
    return async (dispatch) => {
        let data = await addContact({name,number,token})
        dispatch({
            type: 'ADD',
            data
        })
    }
}

export const deleteContactReducer = ({id, token}) => {
    return async (dispatch) => {
        await deleteRequest({id, token})
        dispatch({
            type: 'DELETE',
            id
        })
    }
}

export const editContactReducer = (newContact) => {
    return async (dispatch) => {
        let succed = await editRequest(newContact)
        
        dispatch({
            type: 'EDIT',
            data: succed? newContact: false
        })
    }
}

export default contactsReducer